:root {
  --gray-color: #d9d9d9;
  --black-color: #000;
  --white-color: #fff;
  --brand-color: #d41443;
  --primary1-color: #ffb8ca;
  --primary2-color: #f0d1a5;
  --primary3-color: #c2e297;
  --secondary1-color: #ffa5ba;
  --secondary2-color: #fafafa;
  --bg-color: #fff5f6;
  --bg-color2: #e5e5e5;
  --modal-color: #e17992;
  --text-color1: #aeadad;
  --text-color1: #907e82;
  --text-color2: #212121;
  --hover-color: #ecebeb;
  --main-font: "DM Sans", sans-serif;
  --second-font: "Titan One", sans-serif;
  --max-width: 100%;
  --paragraph-color: #907e82;
}

.carousel__inner__indicators, .carousel__inner__control {
  cursor: pointer;
  text-align: center;
  z-index: 10;
  position: absolute;
}

.footer__container__msg--title-clickable, .footer__container__msg--title {
  font-family: var(--second-font);
  color: var(--secondary1-color);
  font-size: 18px;
  line-height: 21px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.footer__container--right, .footer__container--top {
  flex-flow: column wrap;
  display: flex;
}

.footer__social__list--link:focus, .footer__social__list--link:hover {
  background-color: var(--brand-color);
  cursor: pointer;
}

.flipper--back, .flipper--front, .product__container--flip {
  width: 280px;
  height: 489px;
  background-color: var(--primary1-color);
  border-radius: 25px;
}

.flipper--back--2, .flipper--front--2, .product__container--flip--2 {
  background-color: var(--primary2-color);
}

.flipper--back--3, .flipper--front--3, .product__container--flip--3 {
  background-color: var(--primary3-color);
}

.flipper--back, .flipper--front {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

* {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
}

:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  padding-left: 0;
}

