  padding-right: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  font-family: var(--main-font);
  letter-spacing: .04em;
  color: var(--white-color);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.64;
}

img {
  max-width: var(--max-width);
  height: auto;
  display: block;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 480px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container--link {
  text-decoration: none;
}

.container--unstyled-list {
  list-style-type: none;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.hero-container {
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .hero-container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .hero-container {
    max-width: 1200px;
  }
}

.header {
  position: relative;
}

.header__inner {
  z-index: 199;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__nav {
  display: none;
}

.header .button--nav {
  color: var(--brand-color);
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin-left: auto;
}

.header .nav--icon {
  fill: currentColor;
  width: 20px;
  height: 12px;
  display: block;
}

.header__button {
  display: none;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 32px;
  }

  .header__button {
    background-color: var(--white-color);
    color: var(--brand-color);
    border: none;
    border-radius: 22px;
    align-items: center;
    margin-left: 32px;
    font-weight: 700;
    display: flex;
    padding: 12px 37.23px !important;
    font-size: 16px !important;
  }

  .header__button__icon {
    width: 8px;
    height: 16px;
    margin-left: 12px;
  }

  .header__button:hover {
    font-weight: 700;
    transition: all 1s cubic-bezier(.4, 0, .2, 1);
    transform: translateY(-15px);
  }
}

@media screen and (min-width: 1200px) {
  .button--nav {
    display: none;
  }

  .header__nav {
    gap: 35px;
    display: flex;
    position: relative;
  }

  .header__nav a {
    color: var(--white-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 1.31;
  }

  .header__nav a:hover {
    color: var(--brand-color);
  }
}

.hero {
