.footer__container {
  background-color: var(--bg-color);
  position: relative;
}

.footer__container--title {
  font-family: var(--second-font);
  text-transform: uppercase;
  color: var(--black-color);
  font-size: 18px;
  line-height: 1.17;
  display: none;
}

.footer__container--title span {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.footer__container--top {
  justify-content: flex-start;
  padding-bottom: 40px;
}

.footer__container--right {
  justify-content: flex-start;
  gap: 40px;
}

.footer__container__msg {
  width: 232px;
  height: 42px;
  display: inline-block;
  position: relative;
}

.footer__container__msg--subtitle {
  font-family: var(--second-font);
  text-transform: uppercase;
  color: var(--brand-color);
  width: 200px;
  height: 76px;
  flex-flow: wrap;
  margin: 16px 0 40px;
  font-size: 34px;
  line-height: 1.15;
  display: flex;
}

.footer__container__msg--subtitle span:nth-child(5):after {
  content: "\a ";
  margin-right: 2.2em;
}

.footer__container__copyright {
  text-align: center;
  border-top: 1px solid #e1e1e1;
  justify-content: center;
  padding-top: 16px;
  display: flex;
}

.footer__container__copyright_text {
  max-width: 150px;
  color: #907e82;
  font-weight: 700;
  line-height: 1.52;
}

.footer__social__list {
  justify-content: flex-end;
  gap: 28px;
  display: flex;
}

.footer__social__list--link {
  width: 32px;
  height: 32px;
  background-color: #fea5bb;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__social__list--icon {
  width: 18px;
  height: 18px;
  fill: var(--white-color);
}

.footer__contact--link {
  text-align: right;
  letter-spacing: .03em;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.86;
}

.footer__contact--text {
  color: var(--black-color);
}

.footer__contact--phone {
  color: var(--brand-color);
  z-index: 1;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
  position: relative;
}

.footer__contact--phone:hover {
  z-index: 2;
  cursor: pointer;
  transform: scale(1.1)translate(7px);
}

@media (min-width: 768px) {
  .footer__container--top {
    flex-direction: row;
    gap: 130px;
    padding-bottom: 40px;
    display: flex;
  }

  .footer__container--right {
    justify-content: space-evenly;
    gap: 0;
  }

  .footer__container__msg {
    width: 340px;
    height: 57px;
  }

  .footer__container__msg--tile, .footer__container__msg--tile-clickable {
    font-size: 26px;
    line-height: 30px;
  }

  .footer__container__msg--subtitle {
    width: 280px;
    height: 110px;
    letter-spacing: .06em;
    text-transform: uppercase;
    margin: 0;
    font-size: 48px;
    line-height: 55px;
  }

  .footer__container--copyright {
    text-align: center;
    border-top: 1px solid #e1e1e1;
    justify-content: center;
    padding-top: 8px;
    display: flex;
  }

  .footer__social__list {
    gap: 30px;
  }

  .footer__social__list--link {
    width: 32px;
    height: 32px;
  }
}

@media (min-width: 1280px) {
  .footer__container--top {
    flex-flow: row;
    justify-content: space-between;
    padding-bottom: 60px;
    display: flex;
  }

  .footer__container--right {
    gap: 40px;
    margin-top: 10px;
  }

  .footer__container__msg {
    width: 472px;
    height: 34px;
  }

  .footer__container__msg--title, .footer__container__msg--title-clickable {
    font-size: 30px;
    line-height: 34px;
  }

  .footer__container__msg:hover {
    cursor: pointer;
  }

  .footer__container__msg:hover .footer__container__msg--title {
    animation-name: move-right;
    animation-duration: .1s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
  }

  @keyframes move-right {
    0% {
      transform: translateX(0);
    }

    100% {
      transform: translateX(75px);
    }
  }

  .footer__container__msg:hover .footer__container__msg--title-clickable {
    color: var(--brand-color);
    animation-name: move-down;
    animation-duration: 1s;
    animation-timing-function: cubic-bezier(.4, 0, .2, 1);
    animation-fill-mode: forwards;
    position: relative;
  }

  @keyframes move-down {
    0% {
      transform: translateX(0)translateY(0);
    }

    50% {
      transform: translateX(-130%)translateY(1.1em);
    }

    100% {
      transform: translateX(-130%)translateY(1.1em);
    }
  }

  .footer__container__msg--title .footer__container__msg--title-clickable {
    cursor: pointer;
    position: relative;
  }

  .footer__container__msg--subtitle {
    letter-spacing: .06em;
    width: 500px;
    height: 66px;
    margin-top: 20px;
    font-size: 58px;
    line-height: 66px;
  }

  .footer__container__msg--subtitle span:nth-child(5):after {
    content: none;
    margin: 0 auto;
  }

  .footer__container__msg--subtitle span:hover {
    transition: all .25s cubic-bezier(.4, 0, .2, 1);
    transform: scale(1.2);
  }

  .footer__contact--link {
    font-size: 14px;
    line-height: 1.86;
  }

  .footer__contact--phone:hover {
    transform: scale(1.2)translate(10px);
  }
}

.customer__title {
