.carousel {
  margin: 60px 0 360px;
  position: relative;
}

.carousel__inner {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.carousel__inner--svg {
  cursor: pointer;
  fill: var(--gray-color);
  width: 12px;
  height: 11px;
}

.carousel__inner--open:checked + .carousel__item {
  opacity: 1;
  position: static;
}

.carousel__inner__control {
  display: none;
  top: 58%;
  transform: translate(0, -58%);
}

.carousel__inner__control--prev {
  left: 2%;
}

.carousel__inner__control--next {
  right: 2%;
}

.carousel__inner__control svg {
  fill: var(--gray-color);
  stroke: rgba(0, 0, 0, 0);
  width: 40px;
  height: 40px;
}

.carousel__inner__indicators {
  margin: 0;
  padding: 0;
  bottom: .5%;
  left: 0;
  right: 0;
}

.carousel__inner__indicators li {
  margin: 0 5px;
  display: inline-block;
}

.carousel__item {
  opacity: 0;
  padding-bottom: 20px;
  position: absolute;
}

.carousel__item__bullet {
  flex-flow: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 16px 0;
  display: flex;
}

.carousel__item__bullet svg {
  fill: #c7133e;
  width: 4px;
  height: 4px;
}

.carousel__item__review {
  height: auto;
  max-width: var(--max-width);
