  height: 266px;
  margin: 0 auto;
}

.carousel__item__review__msg {
  font-family: var(--main-font);
  text-align: center;
  color: var(--black-color);
  width: 157px;
  height: 239px;
  z-index: 1;
  margin: 0 auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 2.55;
  position: relative;
}

.carousel__item__review__msg:before {
  content: "";
  width: 54px;
  height: 40px;
  z-index: -1;
  background-image: url("apostrof.f24d3561.png");
