  background-image: url("Background-760.027572b2.png");
  background-repeat: no-repeat;
  margin-bottom: 100px;
}

.container-flex {
  max-width: 480px;
  text-align: center;
  margin: 0 auto;
}

.our-goal {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.list-section {
  width: 100%;
  margin: 0 auto;
}

.paragraph-section {
  width: 100%;
  margin: 0 auto 30px;
  padding: 30px;
}

.pink-image {
  display: none;
}

.description-list {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  list-style-type: none;
  display: flex;
}

.description-item {
  height: 133px;
  border: 1px solid var(--primary1-color);
  background-color: var(--bg-color);
  text-align: center;
  box-shadow: 1px 0 2px var(--primary1-color), 1px 0 1px var(--primary1-color), 3px 0 1px var(--primary1-color);
  border-radius: 20px;
  flex-basis: calc(50% - 32.5px);
  transition-property: transform;
}

.description-item:hover {
  transition-duration: .5s;
  transform: scale(1.2);
}

.description-item:nth-child(2n) {
  position: relative;
  top: 30px;
}

.awards {
  color: var(--black-color);
  font-family: var(--main-font);
  font-weight: 700px;
  margin: 0;
  padding: 0;
  font-size: 42px;
  line-height: 1.85;
}

.awards:after {
  content: "+";
  color: var(--brand-color);
  font-weight: 600px;
  font-size: 16px;
  position: relative;
  top: -20px;
}

.awards-item {
  color: var(--paragraph-color);
  font-family: var(--main-font);
  font-weight: 400px;
  margin: 0;
  padding: 0;
  font-size: 11px;
  line-height: 1.85;
}

.imaging {
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 780px) {
  .goal-section {
    background-image: url("Background-960.3e672b5b.png");
  }
}

@media screen and (min-width: 780px) {
  .description-list {
    position: relative;
    top: -65px;
    left: 100px;
  }

  .pink-image {
    margin-top: 40px;
    transition-property: transform;
    display: flex;
    position: relative;
    top: -300px;
    left: -250px;
  }

  .pink-image:hover {
    transition-duration: .5s;
    transform: rotate(180deg);
  }
}

